    import service from '../../service.js';
    import mInput from '../../components/m-input.vue'
	import { dlrHandleLogin, dlrLoginCode } from '@/utils/api'

    export default {
        components: {
            mInput
        },
        data() {
            return {
                providerList: [],
                hasProvider: false,
                mobile: '',
                code: '',
                positionTop: 0,
				codeText: '发送验证码',
				codeTimer: null
            }
        },
        methods: {
			goDlrRegister(e){
				uni.redirectTo({
					url: '/pages/dlr_register/dlr_register'
				})
			},
			fixIos: function(){
				setTimeout(function() {                
					var scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
					window.scrollTo(0, Math.max(scrollHeight - 1, 0));            
				}, 100);
			},
			async getCode(){
				if(this.codeText != '发送验证码' &&  this.codeText != '再次发送') return
				
				if(!/^(0|86|17951)?(13[0-9]|14[5-9]|15[012356789]|16[6]|17[0-8]|18[0-9]|19[89])[0-9]{8}$/.test(this.mobile)){
					wx.showToast({
						icon: 'none',
						title: '请输入正确的手机号',
						duration: 1000
					})
					return
				}
				
				dlrLoginCode({mobile: this.mobile})
				wx.showToast({
					icon: 'none',
					title: '发送成功',
					duration: 1000
				})
				
				this.codeText = 30
				this.codeTimer = setInterval(()=>{
					if(this.codeText == 1) {
						this.codeText = '再次发送'
						clearInterval(this.codeTimer)
					} else {
						this.codeText == --this.codeText
					}
				}, 1000)
			},
			async bindLogin(){
				if(!/^(0|86|17951)?(13[0-9]|14[5-9]|15[012356789]|16[6]|17[0-8]|18[0-9]|19[89])[0-9]{8}$/.test(this.mobile)){
					wx.showToast({
						icon: 'none',
						title: '请输入正确的手机号',
						duration: 1000
					})
					return
				}
				
				if(!this.code.trim()){
					wx.showToast({
						icon: 'none',
						title: '请输入正确的验证码',
						duration: 1000
					})
					return
				}
				
				let params = {
					mobile: this.mobile,
					code: this.code
				}
				
				let res = await dlrHandleLogin(params)
				
				if(res.code == 200){
					let token = res.token
					if(token){
						uni.setStorageSync('token', token);
						uni.navigateBack()
					}else {
						wx.showToast({
							icon: 'none',
							title: '注册失败',
							duration: 1000
						})
					}
				}
			}
        },
        onLoad() {
        },
		onHide(){
			clearInterval(this.codeTimer)
		}
    }
